import { createApp } from 'vue'
import App from './App.vue'

import ElementPlus from 'element-plus' 
import 'element-plus/dist/index.css'

// or use:
//createApp(App).use(ElementPlus).mount('#app')
const app = createApp(App)
app.use(ElementPlus)


///////////////////////// vue router
import { createRouter, createWebHashHistory } from 'vue-router'
// import VueRouter from 'vue-router'
import ListCard from './components/ListCard24SolarTerms.vue';
import ListCalendar from './components/ListCalendar.vue';
import AboutApp from './components/AboutApp.vue';

const routes = [
    { path: '/', redirect: '/ListCard' },
    { path: '/ListCard', component: ListCard },
    { path: '/ListCalendar', component: ListCalendar },
    { path: '/AboutApp', component: AboutApp },
  ]
 
  
// 3. 创建路由实例并传递 `routes` 配置
// 你可以在这里输入更多的配置，但我们在这里
// 暂时保持简单
const router = createRouter({
// 4. 内部提供了 history 模式的实现。为了简单起见，我们在这里使用 hash 模式。
history: createWebHashHistory(),
routes, // `routes: routes` 的缩写
})

app.use(router)

app.mount('#app')