<template>
<!-- <el-scrollbar height="600px"> -->
  <!-- <el-slider v-if="false" v-model="size" />  -->
  <el-button text @click="solarTermDescription = 'hello';dialogVisible = true;"
    >click to open the Dialog</el-button
  >  
  <div>   
    <!-- <el-check-tag :checked="springchecked" @change="onChange">春</el-check-tag> -->
    <el-row align="middle" style="font-size: 18px;">      
      <el-col :span="24" >
        <span>季節</span>
        <el-checkbox size="large" v-model="springchecked">春</el-checkbox>
        <el-checkbox size="large" v-model="summberchecked">夏</el-checkbox>
        <el-checkbox size="large" v-model="autumnchecked">秋</el-checkbox>
        <el-checkbox size="large" v-model="winterchecked">冬</el-checkbox>      
      </el-col>
    </el-row>
    <el-row align="middle" style="font-size: 18px;">
    <el-col :span="24" >
        <span>顯示資料</span>
        <el-checkbox size="large" v-model="showEnglishChecked">英文</el-checkbox>
        <el-checkbox size="large" v-model="showDateChecked">日期</el-checkbox>
      </el-col>
    </el-row>    
  </div>
  <el-space wrap :size="size">
    <!-- <el-card v-for="i in itemCount" :key="i" class="box-card" style="width: 250px"  > -->
    <el-card v-for="(oneitem, index)  in filterItem" :key="oneitem.index" class="box-card" style="width: 220px;background-color:#ffffff" 
            @click="solarTermDescription = oneitem['DescriptionC'];dialogVisible = true;" >
      <template #header>
        <div class="card-header bg-purple-light">
          <!-- <div class="card-header-text" style="font-size: 50px;">{{index}}: {{ oneitem["SolarTermC"]}}</div> -->
          <div class="card-header-text" style="font-size: 50px;" :id="index"> {{ oneitem["SolarTermC"]}}</div>
          <div class="card-header-text" v-if="showEnglishChecked" style="font-size: 20px;">{{ oneitem["SolarTermE"]}}</div>
          <!-- <el-button class="button" text>Operation button</el-button> -->
        </div>
      </template>
      <div class="text item">
        <el-tag v-if="oneitem['Season'] == '春'" type="success">春</el-tag>
        <el-tag v-if="oneitem['Season'] == '夏'" class="mx-2" type="danger">夏</el-tag>
        <el-tag v-if="oneitem['Season'] == '秋'" class="mx-2" type="warning">秋</el-tag>
        <el-tag v-if="oneitem['Season'] == '冬'" class="mx-2">冬</el-tag>

        <span v-if="showDateChecked"> {{ oneitem["DateRange"] }} </span>
      </div>
      <!-- <div v-for="o in 4" :key="o" class="text item">
        {{ 'List item ' + o }}
      </div> -->
    </el-card>
  </el-space>
<!-- </el-scrollbar>   -->
  <!--------------- 詳細dialog框 ----------------->
  <el-dialog
    v-model="dialogVisible"
    title="詳細"
    width="70%"
    :before-close="handleClose"
  >
    <span style="font-size: 16px;">{{solarTermDescription}}</span>
    <template #footer>
      <span class="dialog-footer">
        <!-- <el-button @click="dialogVisible = false">Cancel</el-button> -->
        <el-button type="primary" sytle="font-size: 2rem;" @click="dialogVisible = false">返回</el-button>
      </span>
    </template>
  </el-dialog>
</template>




<script  setup >
import { ref, computed  } from 'vue'
//import Common from '../assets/js/common.js'
import { getSolarTermsJson} from '@/assets/js/commones6.js'
// import { ElMessageBox } from 'element-plus'

// const size = ref(10);
// const itemCount = ref(24);
const solarTermDescription = ref();
const dialogVisible = ref(false)
const handleClose =  ()=> {
  // ElMessageBox.confirm('Are you sure to close this dialog?')
  //   .then(() => {
  //     // done()
  //   })
  //   .catch(() => {
  //     // catch error
  //   })
}

const jsonSolarTerms = getSolarTermsJson();
// console.log(Object.keys(jsonSolarTerms).length);
//alert(jsonSolarTerms[0]["SolarTermC"])

//filter
var springchecked = ref(true);
var summberchecked = ref(true);
var autumnchecked = ref(true);
var winterchecked = ref(true);
//顥示
var showEnglishChecked = ref(true);
var showDateChecked = ref(true);


let filterItem = computed(()=>{ 
    var filterList = jsonSolarTerms.filter(
      i => ((i['Season'] == '春' && springchecked.value) 
        || (i['Season'] == '夏' && summberchecked.value) 
        || (i['Season'] == '秋' && autumnchecked.value) 
        || (i['Season'] == '冬' && winterchecked.value) )
    ) 
    console.log(Object.keys(filterList).length);
    return filterList;
}) 


// function onChange(value) {  
//   checked.value = value;
//   // console.log('value: '+ value);
//   // console.log('springchecked: '+springchecked.value);
// }


// functions that mutate state and trigger updates
// function increment() {
//   count.value++
// }

// // lifecycle hooks
// onMounted(() => {
//   console.log(`The initial count is ${count.value}.`)
// })

</script>

<style>

.el-card {
  text-align: center; 
}

.grid-content {
  /* border-radius: 4px; */
  min-height: 6px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

</style>