let filterItem = computed(()=>{ 
    var selectDate = new Date(selectedYear.value);
    console.log("computed[selectedYear]: "+selectDate.getFullYear());
    let filterList = [];
    for (var dd = new Date(selectDate.getFullYear(), 0, 1); dd <= new Date(selectDate.getFullYear(), 12, 31); dd.setDate(dd.getDate() + 1)) {
        let d = new Date(dd);
        let month = (d.getMonth() + 1).toString();
        let day = d.getDate().toString();
        let year = d.getFullYear();

    //console.log(getjq(year,month,day));
        let solarterm = getjq(year,month,day);
    
        if (solarterm)
            filterList.push(formatDate(dd) + '  '+ solarterm);
    }
    console.log("computed[filterList]: "+filterList);
    return filterList;
})


